import {
  Divider,
  Drawer,
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  IconButton,
} from "@mui/material";
import React from "react";
import HomeIcon from "@mui/icons-material/Home";
import LogoutIcon from "@mui/icons-material/Logout";
import PersonIcon from "@mui/icons-material/Person";
import CreateIcon from "@mui/icons-material/Create";
import SettingsIcon from "@mui/icons-material/Settings";
import { useLocation, useNavigate } from "react-router-dom";
import { useTheme } from "@emotion/react";
import { Brightness4, Brightness7 } from "@mui/icons-material";

const Draweer = ({ drawerWidth, setmood, noneORblock, temporary, setnoneORblock, permanent }) => {
  const theme = useTheme();
  const location = useLocation();
  const navigate = useNavigate();


const mylist = [
  {
    text: "Home",
    icon: <HomeIcon/>,
    path: "/"
  },
  {
    text: "Creat",
    icon: <CreateIcon/>,
    path: "/Create"
  },
  {
    text: "Profile",
    icon: <PersonIcon/>,
    path: "/Profile"
  },
  {
    text: "Settings",
    icon: <SettingsIcon/>,
    path: "/Settings"
  },
  {
    text: "Logout",
    icon: <LogoutIcon/>,
    path: "/Logout"
  },

]

  return (
    <Drawer
      sx={{
        display: {xs: noneORblock , sm:"block"},
        width: drawerWidth,
        flexShrink: 0,
        "& .MuiDrawer-paper": {
          width: drawerWidth,
          boxSizing: "border-box",
        },
      }}
      variant= {temporary}
      anchor="left"
      open={true}
      onClose={() => {
        setnoneORblock("none")
        permanent("permanent")
      }}
    >
      <List>
        <ListItem sx={{ display: "flex", justifyContent: "center" }}>
          <IconButton
            onClick={() => {
              localStorage.setItem(
                "moodthem",
                theme.palette.mode === "light" ? "dark" : "light"
              );
              setmood(theme.palette.mode === "light" ? "dark" : "light");
            }}
          >
            {theme.palette.mode === "dark" ? (
              <Brightness7 sx={{ color: "orange" }} />
            ) : (
              <Brightness4 />
            )}
          </IconButton>
        </ListItem>
        <Divider />
        

        {mylist.map((item) => (
        <ListItem
        disablePadding
        key={item.text}
        sx={{
          bgcolor:
            location.pathname === item.path ? theme.palette.ahmed.main : null,
        }}
      >
        <ListItemButton 
          onClick={() => {
            navigate(item.path);
          }}
        >
          <ListItemIcon>
            {item.icon}
          </ListItemIcon>
          <ListItemText  primary={item.text} />
        </ListItemButton>
      </ListItem>  
        ))
      }
        
      </List>
    </Drawer>
  );
};

export default Draweer;
